.header_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 24px;
}
.avtor {
  display: flex;
  align-items: center;
  gap: 8px;
  & p {
    color: rgba(38, 41, 49, 1);
    font-family: Inter;
    font-size: 16px;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0%;
    text-align: left;
  }
}

.header_nav {
  display: flex;
  gap: 56px;
  & li {
  }
}
.header_nav_a {
  color: rgba(97, 99, 109, 1);
  font-family: Inter;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  letter-spacing: 0%;
  text-align: left;
}
